#parent-grid {
    padding-left: 20vw;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    max-width: 20rem;
    max-height: 40rem;
}

.grid-cell:nth-child(3n) {
    border-right: black .3rem solid;
}

.grid-cell {
    border: grey .05rem solid;
    text-align: center;
    color: black;
    padding: 1rem;
    
}

.grid-cell-hint {
    border: grey .05rem solid;
    text-align: center;
    line-height: 1rem;
    margin: 0;
    padding: 25%;
    color: rgb(70, 70, 70);
}
.grid-cell-hint:nth-child(3n) {
    border-right: black .3rem solid;
}


.tborder {
    border-top: black .3rem solid
}

.bborder {
    border-bottom: black .3rem solid
}

.lborder {
    border-left: black .3rem solid
}

.grid-cell:focus {
    animation: rainbow-cell infinite 1s;
    border: none;

}


input {
    outline: none;
    font-size: 1.5rem;
    font-weight: bold;
    overscroll-behavior: contain;

}

@keyframes rainbow-cell {
    0% {
        border: .2rem inset lightblue;
    }

    6% {
        border: .2rem inset cyan;
    }

    13% {
        border: .2rem inset indigo;
    }

    20% {
        border: .2rem inset blueviolet;
    }

    25% {
        border: .2rem inset fuchsia;
    }

    32% {
        border: .2rem inset red;
    }

    39% {
        border: .2rem inset indianred;
    }

    44% {
        border: .2rem inset lightcoral;
    }

    50% {
        border: .2rem inset salmon;
    }

    56% {
        border: .2rem inset lightgoldenrodyellow;
    }

    62% {
        border: .2rem inset yellow;
    }

    65% {
        border: .2rem inset lightyellow;
    }

    70% {
        border: .2rem inset yellowgreen;
    }

    75% {
        border: .2rem inset lightgreen;
    }

    78% {
        border: .2rem inset green;
    }

    85% {
        border: .2rem inset lightskyblue;
    }

    92% {
        border: .2rem inset lightcyan;
    }

    100% {
        border: .2rem inset lightblue;
    }
}


h3 {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0;
    margin-inline-end: 0;
}

input[type=number] {
    -moz-appearance: textfield;
  }

  /* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.error {
    color: red
}